.tab__list {
  display: flex;
  flex-wrap: wrap;
}

.tab__item.tab__item--active {
  display: none;
}

.tab__item a {
  position: relative;
  display: block;
  z-index: 0;
  text-decoration: none;
  color: #404e54;
  transition: color 300ms ease;
}

.tab__item a[data-drupal-link-system-path*="delete"] {
  color: #e74c3c;
}

.tab__item a[data-drupal-link-system-path*="delete"]:before {
  background-color: #e74c3c;
}

.tab__item a:before {
  content: " ";
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #404e54;
  transform: scaleY(0);
  transform-origin: bottom;
  transition: transform 200ms ease;
}

.tab__item a.is-active {
  color: #2BA9E0;
}

.tab__item a.is-active:before {
  background-color: #2BA9E0;
}

.tab__item a:hover, .tab__item a:focus {
  color: #fff;
  border-color: #404e54;
}

.tab__item a:hover:before, .tab__item a:focus:before {
  transform: scaleY(1);
  transform-origin: bottom;
}

@media (max-width: 38.615em) {
