.contextual-links {
  display: none;
  margin-top: -0.0625rem;
  overflow: hidden;
  text-align: left;
  white-space: nowrap;
  background-color: #fff;
  border: 0.0625rem solid #2BA9E0;
  border-radius: 0 0 0 0;
  animation: contextual-menu-right 400ms reverse;
}

[dir="rtl"] .contextual-links {
  text-align: right;
  border-radius: 0 0 0 0;
  animation: contextual-menu-left 400ms reverse;
}

.contextual-links li {
  margin-bottom: 0.0625rem;
}

.contextual-links li:last-child {
  margin-bottom: 0;
}

.contextual-links a {
  display: block;
  padding: 0.5em 0.6em;
  font-family: sans-serif;
  font-size: 1rem;
  line-height: 1.1;
  text-decoration: none;
  color: #000;
  background-color: #fff;
}

.touchevents .contextual-links a {
  font-size: 1.25rem;
}

.contextual-links a:hover {
  text-decoration: none;
}

.no-touchevents .contextual-links a:hover {
  color: #fff;
  background-color: #2BA9E0;
}

.contextual {
  position: absolute;
  display: flex;
  z-index: 50;
  top: -0.9375rem;
  right: -0.9375rem;
  flex-direction: column;
  align-items: flex-end;
}

@media (max-width: 71.6775em) {
  .contextual {
    right: 0;
  }
}

[dir="rtl"] .contextual {
  align-items: flex-start;
}

.contextual .trigger {
  width: 1.875rem !important;
  height: 1.875rem !important;
  overflow: hidden;
  font-size: 0;
  background-color: #fff;
  background-image: url(../images/svg/icon-pencil-default.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  background-attachment: scroll;
  border: 0.0625rem solid #2BA9E0;
  border-radius: 1.25rem;
  cursor: pointer;
  opacity: 1;
  transition: border-radius 100ms ease, opacity 200ms ease;
}

.contextual .trigger:hover {
  background-image: url(../images/svg/icon-pencil-hover.svg);
}

.contextual .trigger:focus {
  position: relative !important;
  background-image: url(../images/svg/icon-pencil-focus.svg);
  outline: none;
}

.contextual .trigger.visually-hidden {
  opacity: 0;
  clip: auto;
}

.contextual.open {
  z-index: 51;
}

.contextual.open .trigger {
  z-index: 2;
  border-width: 0.0625rem;
  border-color: #2BA9E0 #2BA9E0 transparent #2BA9E0;
  border-radius: 1.25rem 1.25rem 0 0;
  box-shadow: none;
}

.contextual.open .contextual-links {
  display: block;
  animation: contextual-menu-right 400ms;
}

[dir="rtl"] .contextual.open .contextual-links {
  animation: contextual-menu-left 400ms;
}

.contextual-region {
  position: relative;
}

.contextual-region.focus {
  outline: 0.125rem dashed #2BA9E0;
  outline-offset: 0.0625rem;
}

.contextual-region.view.focus {
  outline-color: #FF9901;
}

.contextual-region.view > .contextual {
  top: -0.9375rem !important;
  right: 0;
  left: 0;
  align-items: center;
}

.contextual-region.view > .contextual .trigger {
  border-color: #FF9901;
}

.contextual-region.view > .contextual .trigger:focus {
  background-image: url(../images/svg/icon-pencil-alt-1.svg);
}

.contextual-region.view > .contextual .contextual-links {
  border-color: #FF9901;
}

.contextual-region.view > .contextual .contextual-links a {
  background-color: #fff;
}

.no-touchevents .contextual-region.view > .contextual .contextual-links a:hover {
  background-color: #FF9901;
}

.contextual-region.view > .contextual.open .trigger {
  border-color: #FF9901 #FF9901 transparent #FF9901;
}

.contextual-region.view > .contextual.open .contextual-links {
  animation: contextual-menu-center 400ms;
}

